import { Table } from "antd";
import { ColumnsType } from "antd/lib/table";
import { TruncatedText } from "./TruncatedText";

interface IProps {
  listOfParameters: string[];
}

interface IRowData {
  originalString: string;
}

const columns: ColumnsType<IRowData> = [
  {
    key: "originalString",
    title: "Original string",
    dataIndex: "originalString",
  },
  {
    key: "char8",
    title: "8 Characters",
    dataIndex: "char8",
    render: TruncatedText(8),
  },
  {
    key: "char4",
    title: "4 Characters",
    dataIndex: "char4",
    render: TruncatedText(4),
  },
];

export const TruncatedTable: React.FC<IProps> = ({ listOfParameters }) => {
  const dataSource: IRowData[] = listOfParameters.map((x) => ({
    originalString: x,
    char8: x,
    char4: x,
  }));
  return (
    <Table
      style={{ height: "100%", overflowY: "scroll" }}
      sticky={{ offsetHeader: 0 }}
      size="small"
      bordered={true}
      columns={columns}
      dataSource={dataSource}
      pagination={false}
    />
  );
};
