import { TruncateToCharacters } from "../modules/truncate";

interface IProps {
  value: string;
  numChars: number;
}

export const TruncatedText = (num: number) => {
  return (value: string) => RenderTruncatedText(num, value);
};

export const RenderTruncatedText = (num: number, value: string | undefined) => {
  return TruncateToCharacters(value ?? "", 4);
};
