import { useEffect, useState } from "react";
import "./App.css";
import { Card, Input, Layout } from "antd";
import { Content, Header } from "antd/lib/layout/layout";
import { ReadFile } from "./components/ReadFile";
import { TruncatedTable } from "./components/TruncatedTable";

function App() {
  const [text, setText] = useState("");
  const [regEx, setRegEx] = useState("<ID>(.*?)</ID>");
  const [textToRegEx, setTextToRegEx] = useState("");

  useEffect(() => {
    const matches = Array.from(textToRegEx.matchAll(new RegExp(regEx, "g")));
    const newText = matches.map((match) => match[1]).join("\r\n");
    setText(newText);
  }, [regEx, textToRegEx]);

  const listOfParameters = text
    .split(/\r?\n/g)
    .filter((x) => x.trim().length !== 0);

  return (
    <Layout className="fill">
      <Header
        className="header"
        style={{
          display: "flex",
          alignItems: "baseline",
          justifyContent: "center",
          color: "white",
          fontStyle: "italic",
        }}
      >
        <div
          style={{
            fontFamily: "font-family: 'Lato', sans-serif",
            fontWeight: "bold",
            fontSize: "2em",
          }}
        >
          PLUG-IN UTILITIES
        </div>
      </Header>
      <Content
        style={{
          display: "flex",
          height: "100%",
          width: "100%",
          padding: 8,
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            flex: 1,
          }}
        >
          <div style={{ flex: 0 }}>
            <Card>
              <div>Drag and drop PageTable .xml below to upload.</div>
              <ReadFile
                inputProps={{ accept: ".xml" }}
                onReadFile={(x) => {
                  setTextToRegEx(x);
                }}
              />
            </Card>
            <Card style={{ marginTop: 8 }}>
              <div>Regular expression:</div>
              <Input
                value={regEx}
                onChange={(e) => setRegEx(e.currentTarget.value)}
              />
            </Card>
            <Card style={{ marginTop: 8 }}>
              <Input
                value={textToRegEx}
                onChange={(e) => setTextToRegEx(e.currentTarget.value)}
                placeholder="Text content..."
              />
            </Card>
          </div>
          <div style={{ flex: 1 }}>
            <Card style={{ marginTop: 8, height: "100%" }}>
              <Input.TextArea
                value={text}
                onChange={(e) => setText(e.currentTarget.value)}
                style={{
                  resize: "none",
                  width: "100%",
                  height: "100%",
                }}
              />
            </Card>
          </div>
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            flex: 1,
            marginLeft: 8,
          }}
        >
          <Card style={{ height: "100%" }}>
            <TruncatedTable listOfParameters={listOfParameters} />
          </Card>
        </div>
      </Content>
    </Layout>
  );
}

export default App;
