const wordsToRemove = ["and"];

export function TruncateToCharacters(str: string, numChars: number): string {
  var newString = RemovePunctuation(str.trim());
  var words = str.split(/\s/);

  const isGood = () => {
    var total = 0;
    words.forEach((word) => {
      total += word.length;
    });
    return total < numChars;
  };

  const getWord = () => words.join("");

  if (isGood()) {
    return getWord();
  }
  return getWord();
}

function RemoveVowels(str: string) {
  return str.replace(/[aeiou]/g, "");
}

function ContainsPunctuation(str: string) {
  return str.match(/[^\w\s]/gi) != null;
}

function RemovePunctuation(str: string) {
  return str.replace(/[^\w\s]/gi, "");
}
